.JokeList {
	display: flex;
	width: 80%;
	height: 80%;

	&-sidebar {
		background-color: #fa7921;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 30%;
		box-shadow: 0px 0px 55px -27px rgba(0, 0, 0, 0.53);
		z-index: 2;
		border-radius: 10px;

		img {
			box-shadow: 0px 0px 55px -27px rgba(0, 0, 0, 0.53);
			border-radius: 50%;
		}
	}

	&-title {
		display: flex;
		font-size: 200%;
		margin: 2rem;
		color: white;
		font-weight: 300;
		letter-spacing: 0.6rem;

		span {
			margin-right: 1rem;
			font-weight: 700;
			letter-spacing: 0;
		}
	}

	&-container {
		align-self: center;
		height: 90%;
		width: 70%;
		background-color: white;
		box-shadow: 0px 0px 55px -27px rgba(0, 0, 0, 0.53);
		overflow: scroll;
	}

	&-button {
		padding: 1rem 2rem;
		border-radius: 2rem;
		border: none;
		color: white;
		margin: 2rem;
		font-size: 1.5rem;
		font-weight: bold;
		word-spacing: 0.5rem;
		cursor: pointer;
		box-shadow: 0px 0px 55px -27px rgba(0, 0, 0, 0.53);
		background: linear-gradient(135deg, rgba(91, 192, 235, 1) 50%, rgba(155, 197, 61, 1) 50%);

		&:hover {
			box-shadow: 0px 0px 55px -27px rgba(0, 0, 0, 0.9);
			transform: translateY(-2px);
		}
	}

	&-spinner {
		text-align: center;
		color: white;
	}
}
